import _clonedeep from 'lodash.clonedeep'

export default {
  props: {
    /**
     * @description 
     */
    data: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      /**
       * @description 
       */
      d2Data: []
    }
  },
  watch: {
    data () {
      this.handleDataChange()
    },
    d2Data (val) {
      this.$emit('d2-data-change', val)
    }
  },
  mounted () {
    this.handleDataChange()
  },
  methods: {
    /**
     * @description d2Data
     */
    handleDataChange () {
      if (this.d2Data !== this.data) {
        this.d2Data = _clonedeep(this.data)
      }
    },
    /**
     * @description key
     */
    handleColumnKey (row, key) {
      if (key.indexOf('.') < 0) {
        return row[key]
      }
      let keyList = key.split('.')
      let keyResult = row
      for (let i = 0; i < keyList.length; i++) {
        if (i === 0) {
          keyResult = row[keyList[0]]
        } else {
          keyResult = keyResult[keyList[i]]
        }
      }
      return keyResult
    },
    /**
     * @description 
     */
    handleSortChange ({ column, prop, order }) {
      this.$nextTick(() => {
        this.d2Data = this.$refs.elTable.store.states.data
      })
      this.$emit('sort-change', { column, prop, order })
    },
    /**
     * @description 
     * @param {Number} index 
     * @param {Object} row 
     */
    updateRow (index, row) {
      this.$set(this.d2Data, index, row)
      if (this.defaultSort) {
        this.$nextTick(() => {
          this.d2Data = _clonedeep(this.$refs.elTable.store.states.data)
        })
      }
    },
    /**
     * @description 
     * @param {Object} row 
     */
    addRow (row) {
      this.$set(this.d2Data, this.d2Data.length, row)
      if (this.defaultSort) {
        this.$nextTick(() => {
          this.d2Data = this.$refs.elTable.store.states.data
        })
      }
    },
    /**
     * @description 
     * @param {Object} index 
     */
    removeRow (index) {
      this.$delete(this.d2Data, index)
      if (this.defaultSort) {
        this.$nextTick(() => {
          this.d2Data = this.$refs.elTable.store.states.data
        })
      }
    }
  }
}



