import _forEach from 'lodash.foreach'

export default {
  props: {
    /**
     * @description dialog
     */
    formOptions: {
      type: Object,
      default: null
    },
    /**
     * @description 
     */
    formTemplate: {
      type: Object,
      default: null
    },
    /**
     * @description 
     */
    formRules: {
      type: Object,
      default: null
    }
  },
  data () {
    return {
      /**
       * @description dialog
       */
      showDialog: false,
      /**
       * @description 
       */
      formData: {},
      /**
       * @description 
       */
      formMode: 'edit'
    }
  },
  methods: {
    /**
     * @description 
     */
    handleDialogSave () {
      this.$refs.form.validate((valid) => {
        if (!valid) {
          return false
        }
        const rowData = {}
        if (this.formMode === 'edit') {
          _forEach(this.formData, (value, key) => {
            rowData[key] = value
          })
          this.$emit('row-edit', {
            index: this.editIndex,
            row: rowData
          }, () => {
            this.handleDialogSaveDone(rowData)
          })
        } else if (this.formMode === 'add') {
          _forEach(this.formData, (value, key) => {
            rowData[key] = value
          })
          this.$emit('row-add', rowData, () => {
            this.handleDialogSaveDone(rowData)
          })
        }
      })
    },
    /**
     * @description 
     */
    handleDialogCancel (done) {
      this.$emit('dialog-cancel', done)
    },
    /**
     * @description 
     */
    handleDialogSaveDone (rowData) {
      if (this.formMode === 'edit') {
        this.updateRow(this.editIndex, rowData)
      } else if (this.formMode === 'add') {
        this.addRow(rowData)
      }
      this.closeDialog()
    },
    /**
     * @description 
     */
    closeDialog () {
      this.showDialog = false
    }
  }
}



